import React from "react"
import WinnersStyledBackgroundSection from "../components/winners-background-image"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Img from "gatsby-image"
import Link from "gatsby-link"

const Awards = ({data}) => (
  <Layout>
    <SEO title="Winners" />
    <WinnersStyledBackgroundSection />
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.davison.childImageSharp.fluid}
          alt="Madeleine Davison"
          style={{ width: `100%`, }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Madeleine Davison</div>
        <div className="title mb-4">2021 Student Media Award Winner</div>
        <p className="copy">Madeleine Davison, a recent graduate of Syracuse University, is the collegiate winner for the story <Link to="http://dailyorange.com/2020/03/nursing-center-violations-increase-profit-company-ownership/" style={{ color: `white`, textDecoration: `underline` }}>“Care and Capital.”</Link> Davison’s story explored nursing center violations among central New York for-profit facilities, and judges noted the “work and skill” were above and beyond student quality journalism. “Colorful, detailed interviews” pulled together “great use of data, information (and) information requests” into a well-crafted story. One judge noted, “The imagery in this piece cannot escape my mind.”</p>
      </div>
    </div>
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.plohetski.childImageSharp.fluid}
          alt="Tony Plohetski"
          style={{ width: `100%`, }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Tony Plohetski</div>
        <div className="title mb-4">2021 Professional Award Winner</div>
        <p className="copy">Tony Plohetski of the Austin American-Statesman/KVUE received the professional prize for his <Link to="https://www.statesman.com/story/news/investigations/2020/06/08/texas-police-chase-ends-death-i-cant-breathe-man-cries/3137476001/" style={{ color: `white`, textDecoration: `underline` }}>“Lights. Camera. Violence.”</Link> series. The panel of judges lauded Plohetski’s work on the three-story series, which investigated the death of a 40-year-old Black father while in the custody of county law enforcement, as a “great example of dogged reporting.” Through “extraordinary” reporting and writing, Plohetski created a “compelling, rich narrative that strikes just the right tone between sorrow and outrage.”</p>
      </div>
    </div>
  </Layout>
)

export default Awards

export const query = graphql`
  query {
    davison: file(relativePath: { eq: "Davison.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    plohetski: file(relativePath: { eq: "Plohetski.png" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
